import { getStaticPaths } from '@uniformdev/next-server';
import { getDisconnectedExportPathMap, DisconnectedExportPathMapConfig } from './disconnectedService';

export const getSitecoreStaticPaths = async (config: DisconnectedExportPathMapConfig) => {
    if (process.env.JSS_MODE === 'disconnected') {
        const pathMap = await getDisconnectedExportPathMap(config)({});
        return getStaticPaths(pathMap);
    } else {
        return getStaticPaths();
    }
};
